@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

*{padding:0;margin:0;list-style:none;outline:none;box-sizing:border-box}html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}body{-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased}h1{font-size:48px}h2{font-weight:600;font-size:28px;line-height:30px}a{text-decoration:none}body{background-color:#e5e5e5;font-family:'Open Sans', sans-serif}.container{max-width:760px;margin:0 auto}.logo{display:flex;justify-content:center;align-items:center;margin:30px 0 30px 0}.main-content{display:flex;justify-content:space-between}.main-content .transfer-filter{width:232px;height:252px;display:flex;flex-direction:column;border-radius:5px;box-shadow:0px 2px 8px rgba(0,0,0,0.1);background-color:#fff;color:#4a4a4a}.main-content .transfer-filter .filter-title{font-size:12px;font-weight:600;text-transform:uppercase;line-height:normal;padding:20px 20px 0 20px;margin-bottom:5px}.main-content .transfer-filter .option-item:last-child{padding-bottom:15px}.main-content .transfer-filter .option-item{display:flex;align-items:center;font-size:13px;font-weight:400;padding:10px 20px 10px 20px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.main-content .transfer-filter .option-item.active .checkbox svg{visibility:visible}.main-content .transfer-filter .option-item .checkbox{width:20px;height:20px;display:flex;justify-content:center;align-items:center;border:1px solid #9abbce;border-radius:2px}.main-content .transfer-filter .option-item .checkbox svg{visibility:hidden}.main-content .transfer-filter .option-item:hover{background-color:#f1fcff}.main-content .transfer-filter .option-item span{margin-left:10px}.main-content .content-wrap .sort{display:flex;justify-content:space-between;border-radius:5px;background-color:#fff;min-width:505px;overflow:hidden}.main-content .content-wrap .sort__item{background-color:#fff;width:50%;height:100%;color:#4a4a4a;cursor:pointer;padding:15px 57px;max-height:53px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.main-content .content-wrap .sort__item.active{background-color:#2196f3;color:#fff}.main-content .content-wrap .tickets-list{display:flex;flex-direction:column}.main-content .content-wrap .tickets-list .absence-tickets-placard{margin-top:10px;display:flex;justify-content:center;align-items:center;border-radius:5px;background-color:#fff;box-shadow:0px 2px 8px rgba(0,0,0,0.1);height:300px}.main-content .content-wrap .tickets-list .absence-tickets-placard .message{font-size:20px;color:#4a4a4a}.main-content .content-wrap .tickets-list__item:last-child{margin-bottom:20px}.main-content .content-wrap .tickets-list__item{width:505px;background-color:#fff;border-radius:5px;padding:20px 50px 20px 20px;margin-top:20px;box-shadow:0px 2px 8px rgba(0,0,0,0.1)}.main-content .content-wrap .tickets-list__item .item-head{display:flex;justify-content:space-between}.main-content .content-wrap .tickets-list__item .item-head .price{font-size:24px;font-weight:600;color:#2196f3;text-transform:uppercase}.main-content .content-wrap .tickets-list__item .flight-info{display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:5px}.main-content .content-wrap .tickets-list__item .flight-info .info-item{margin-top:10px;width:110px}.main-content .content-wrap .tickets-list__item .flight-info .info-item__title{font-size:12px;font-weight:600;color:#a0b0b9;text-transform:uppercase}.main-content .content-wrap .tickets-list__item .flight-info .info-item__content{font-size:14px;font-weight:600;color:#4a4a4a}

